@use 'variables' as v;
@use 'base/mixins' as m;

body {
  background-color: v.$white;
  font-family: v.$montserrat;
  font-size: 1.6rem;

  &.body-hold {
    overflow: hidden;
  }

  @include m.tablet {
    &.fixed-body {
      padding-top: 8.6rem;
    }
  }
}

p {
  font-size: 2rem;
  color: v.$black;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  height: auto;
  width: 100%;
}